#imageSlider {
  white-space: nowrap;
  display: flex;

}


#imageSlider img {
  height: 100%;
  flex-shrink: 0;
  width: 300px;
  margin-right: 8px;
}


.back {
  background-color: #fff;
  border: 1px solid;
  color: gray;
  border-radius: 50%;
  width: 30px;
  font-weight: bolder;
}

.progress-bar {
  background: #000;
  width: 100%;
  height: 10px;
}

.next {
  background-color: #fff;
  border: 1px solid;
  color: grey;
  border-radius: 50%;
  width: 30px;
  margin-left: 12rem;
  font-weight: bolder;
}

.BUttons {
  display: flex;

  justify-content: center;
  width: 100%;

}

.imgs {
  overflow-x: hidden;
  width: 59%;
  position: relative;
  left: 19%;

  &::-webkit-scrollbar {
    width: 4px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #078edd;
    border-radius: 6px;

  }

  &::-webkit-scrollbar-thumb:hover {
    background-color: #078edd;
  }

  &::-webkit-scrollbar-track {
    background-color: #ffffff;
  }

  &::-webkit-scrollbar-corner {
    background-color: #ffffff;
  }
}

.category {
  margin-top: 23px;
  border: 1px solid #dddddd;
  border-radius: 15px;
  width: 140px;

  text-align: center;
  cursor: pointer;
  margin-top: 4px;
}

.slide_Img {
  width: 46%;
  height: 100%;
}

.category:active {
  background: #078edd;
  color: aliceblue;
}

.internships {
  margin-top: 45px;
  background: #fafafa;
  white-space: nowrap;
  position: relative;
  overflow-x: hidden;
  scrollbar-width: thin;
  scrollbar-color: #888 #f1f1f1;


  &::-webkit-scrollbar {
    width: 4px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #078edd;
    border-radius: 6px;

  }

  &::-webkit-scrollbar-thumb:hover {
    background-color: #078edd;
  }

  &::-webkit-scrollbar-track {
    background-color: #ffffff;
  }

  &::-webkit-scrollbar-corner {
    background-color: #ffffff;
  }
}

.jobs {
  background: #fafafa;
  white-space: nowrap;
  position: relative;

}

.int-1 {
  width: 80%;
  margin-left: 23px;
  background: #ffff;
  box-shadow: 0px 0px 2px -2px black;
  border-radius: 12px;
}

.int-1 p {
  margin-left: 7px;
}

.int-1 span {
  margin-left: 7px;
}

.int-1 small {
  margin-left: 7px;
}

#boxer {
  width: 150px;
  margin-left: 12px;
  border: 1px solid #dddddd;
}

.log {
  position: relative;
  left: 35%;
}

@media screen and (max-width: 768px) {
  .internships {
    width: 90%;
    background: #fafafa;
    white-space: nowrap;
    position: relative;

    left: 3%;
  }

  .internships {
    overflow-x: scroll;
  }

  .int-1 {

    width: 100%;
    height: 90%;
    margin-left: 4rem;
  }

  .back {
    display: none;
  }

  .next {
    display: none;
  }

  .slide_Img {
    width: 45%;
  }

  #imageSlider img {
    height: 100%;
    flex-shrink: 0;
    width: 200px;
    margin-right: 8px;
  }

  .log {
    position: relative;
    left: 0%;
  }

  .imgs {
    overflow-x: scroll;
  }
}

.info-inter {
  background: #fafafa;
}

.info-job {
  background: #fafafa;
}

.infoys {
  background-color: #fafafa;
}

.internShip-Info {
  width: 116%;
  position: relative;
  height: 291px;

}

.pplr {
  margin-left: 15px;
}






#buttons {
  position: relative;
  left: 53%;
  top: 24%;
  width: 180px;
  font-size: 16px;
  display: flex;
}

.btn6 {
  margin-left: 24px;
  width: 180px;
  margin-top: 5px;
  background: #00489a;
  outline: none;
}

.log {
  background: #0665c9;
}

.logins {
  background: #0665c9;
  text-align: center;
  font-weight: bold;

}

.BUttons {
  margin: 22PX;
}

#admin {
  margin-top: 26px;
}

@media (max-width: 480px) {



  .logins {
    display: none;
  }

  .slide_Img {
    width: 80%;
  }

}